*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    scroll-behavior: smooth;
  }
  .navbar{
    width: 100%;
    max-width: 100vw;
    height: 10vh;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2%;
    position:fixed;
    top: 0%;
    color: wheat;
    z-index: 10;
  }
  .navbar-responsive-container{
      display: none;
  }
  .nav-h1{
    font-size: 16px;
    width: 30%  ;
    display: block;
  }
  .links-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 30%;
  }
  .links-container a{
    color: wheat;
    font-size: 20px;
  }
  .links-container a:hover{
    color: gray;
  }
  .links-responsive{
    display: none;
  }
  .icon{
    width: 30%;
    height: 100%;
  }
  .icon img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
  .open-menu{
    display: none;
  }
  .close-menu{
    display: none;
  }
  .home{
    width: 100%;
    height: 100vh;
    background-color: black;
    overflow-y: hidden;
  
   
  }
  .home-info-wrapper{
    width:50%;
    position: absolute;
    height: fit-content;
    position:absolute;
    top: 40%;
    left: 45%;
  }
  .home-h1{
    width: 100%;
    font-size: 30px;
    color: wheat;
    margin-bottom: 5%;
  }
  .home-p{
    font-size: 26px;
    color: wheat;
  }
  .home-p a{
    color: wheat;
    opacity: 0.7;
  }
  .home-p a:hover{
    opacity: 1;
  }
  .h1-scroll{
    display: none;
    font-size: 16px;
    color: wheat;
  }
  .video-background{
   width: 100%;
   min-height: 100vh;
  }
  video{
    object-fit: cover;
  
  }
  .about{
    width: 100%;
    height: 100vh;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(./Assets/background2.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position:right;
  }
  .about-info-wrapper{
    width: 40%;
    height: 50%;
    margin: 0% 2%;
    color: wheat;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-h1{
    font-size: 30px;
    margin-bottom: 2%;

  }
  .about-p{
    font-size: 26px;
    padding: 5%;
    margin-bottom: 2%;
  }
  .about-btn{
    background-color: wheat;
    color: black;
    width: fit-content;
    height: fit-content;
    padding: 1rem;
    border-radius: 10%;
  }
  .about-img-container{
    width: 25%;
    height: 40%;
    margin-right: 20%;
    border: 1px solid wheat;
  }
  .about-img-container img{
    width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
  }
  .contact{
    width: 100%;
    height: 100vh;
    display: flex;
    background-image: url(./Assets/background3.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: black;
    position: relative;
  }

  .contact-h1-wrapper{
    display: flex;
    width: 50%;
    height: 100%;
    color: wheat;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  #contact-h1{
    font-size: 3em;
  }
  .contact-p-wrapper{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 15%;
  }
  .contact-p-wrapper p{
    margin-top: 50%;
    font-size: 26px;
  }
  .contact-p-wrapper a{
    color: wheat;
  }
  .contact-p-wrapper a:hover{
    opacity:0.5;
  }
  .to-top-btn{
    width: 40px;
    height: 40px;
    color: wheat;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    
    background-color:#908BFC;
    position: absolute;
    bottom: 10%;
    right: 10%;
    cursor: pointer;
    
  }
  
  .spinner {
    --R: 88px;
    --g1: #f4ddb2 96%, #0000;
    --g2: #000000 96%, #0000;
    width: calc(2*var(--R));
    height: calc(2*var(--R));
    border-radius: 50%;
    display: grid;
    -webkit-mask: linear-gradient(#000 0 0);
    animation: spinner-maxc6n 2s infinite linear;
 }
 
 .spinner::before,
 .spinner::after {
    content: "";
    grid-area: 1/1;
    width: 50%;
    background: radial-gradient(farthest-side,var(--g1)) calc(var(--R) + 0.866*var(--R) - var(--R)) calc(var(--R) - 0.5*var(--R)   - var(--R)),
         radial-gradient(farthest-side,var(--g1)) calc(var(--R) + 0.866*var(--R) - var(--R)) calc(var(--R) - 0.5*var(--R)   - var(--R)),
         radial-gradient(farthest-side,var(--g2)) calc(var(--R) + 0.5*var(--R)   - var(--R)) calc(var(--R) - 0.866*var(--R) - var(--R)),
         radial-gradient(farthest-side,var(--g1)) 0 calc(-1*var(--R)),
         radial-gradient(farthest-side,var(--g2)) calc(var(--R) - 0.5*var(--R)   - var(--R)) calc(var(--R) - 0.866*var(--R) - var(--R)),
         radial-gradient(farthest-side,var(--g1)) calc(var(--R) - 0.866*var(--R) - var(--R)) calc(var(--R) - 0.5*var(--R)   - var(--R)),
         radial-gradient(farthest-side,var(--g2)) calc(-1*var(--R))  0,
         radial-gradient(farthest-side,var(--g1)) calc(var(--R) - 0.866*var(--R) - var(--R)) calc(var(--R) + 0.5*var(--R)   - var(--R));
    background-size: calc(2*var(--R)) calc(2*var(--R));
    background-repeat: no-repeat;
 }
 
 .spinner::after {
    transform: rotate(180deg);
    transform-origin: right;
 }
 .loading{
   width: 100%;
   height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #000000;
 }
 
 @keyframes spinner-maxc6n {
    100% {
       transform: rotate(-1turn);
    }
 }
  @media screen and (max-width:700px) {
    html,body{
      overflow-x: hidden;
    }
    .links-container{
      display: none;
    }
    .navbar-responsive-container{
        display: flex;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: black;
        z-index: 9;
    }
    .closed{
      display: none;
    }
    .open{
      display: flex;
    }
    .links-responsive{
     display: flex;
     flex-direction: column;
     justify-content: space-around;
     align-items: center;
     width: 100%;
     height: 100%;
     padding: 20%;
    }
    .links-responsive a{
        color: wheat;
        font-size: 24px;
    }
    .open-menu{
        display: block;
        margin-right: 2%;
        font-size: 20px;
    }
    .close-menu{
      display: block;
      position: absolute;
      top: 5%;
      right: 5%;
      font-size: 20px;
    }
    .home{
      background-image: url(./Assets/Logo-wheat.png);
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }
    .home-info-wrapper{
      display: none;
    }
    .video-background{
      display: none;
    }
    .h1-scroll{
      display: block;
      position: absolute;
      bottom: 10%;
      left: 40%;
    }
    .about{
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-image: none;

    }
    .about-info-wrapper{
      width: 100%;
      height: 30%;
      padding: 2%;

    }
    .about-img-container{
      
      width: 60%;
      height: 30%;
      margin-left: 20%;
      margin-top: 20%;
    }
    .contact{
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .contact-h1-wrapper{
      font-size: 20px;
      width: 100%;
    }
    .contact-p-wrapper{
      width: 80%;
      margin-bottom: 35%;
      padding-bottom: 20%;
      margin-left: 90%;
    }
    .contact-p-wrapper p{
      font-size: 22px;
    }
    .icon-c{
      position: absolute;
      left: 10%;
    }
  }
  @media screen and (min-width:700px) and (max-width:1100px){
    .about{
      background-size: 35%;
      background-position: bottom;
    }
    .about-info-wrapper{
      width: 50%;
      margin: 0;
    }
    .about-img-container{
      margin-right: 0;
      width: 40vw;
      height:30vh;
    }
    .contact{
      background-size: 62%;
      background-position: bottom;
    }
  }